import { createContext } from "react";

const AuthContext = createContext<{
  user: any;
  token: string | null;
  setUser: (user: any) => void;
  setToken: (token: string) => void;
}>({
  user: null,
  token: null,
  setUser: (user: any) => {},
  setToken: (token: string) => {},
});

export default AuthContext;
