import { useContext, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Socket, io } from "socket.io-client";
import AuthContext from "../context/authContext";

interface IUser {
  id: number;
  name: string;
  userName?: string;
  role?: string;
  createdAt?: string;
  updatedAt?: string;
}
interface IMessage {
  id: number;
  userId: number;
  message: string;
}

export const useChat = (message: string) => {
  const socket = useRef<Socket | null>(null);
  const navigate = useNavigate();
  const authContext = useContext(AuthContext);
  const [roomUUID, setRoomUUID] = useState<string>("");
  const [selectedUser, setSelectedUser] = useState<IUser | undefined>(
    undefined
  );
  const [messagesArr, setMessagesArr] = useState<IMessage[]>([]);
  const [typingStatus, setTypingStatus] = useState<string>("");

  useEffect(() => {
    if (!authContext.token) return navigate("/login");
    socket.current = io("http://localhost:4000", {
      extraHeaders: {
        Authorization: `Bearer ${authContext.token}`,
      },
    });
    socket.current.on("newMessage", (response) => {
      setMessagesArr((old) => {
        old.push(response);
        return [...old];
      });
    });
    socket.current.on("typingStatus", (response) => {
      setTypingStatus(response);
    });
  }, [authContext.token, navigate]);

  useEffect(() => {
    socket.current?.emit("typing", { isTyping: true, roomUUID });
    const timer = setTimeout(() => {
      socket.current?.emit("typing", { isTyping: false, roomUUID });
    }, 500);
    return () => {
      clearTimeout(timer);
    };
  }, [message]);

  const joinRoom = (connectToUser: IUser) => {
    setSelectedUser(connectToUser);
    socket.current?.emit(
      "join-room",
      {
        roomDetails: {
          userOneId: authContext.user?.id,
          userSecondId: connectToUser.id,
        },
      },
      (roomUUID: string) => {
        setRoomUUID(roomUUID);
        socket.current?.emit(
          "getAllMessages",
          {
            roomUUID,
          },
          (response: any) => {
            setMessagesArr(response);
          }
        );
      }
    );
  };

  const sendMessage = async (msg: string) => {
    socket.current?.emit(
      "sendMessage",
      {
        roomUUID,
        senderId: authContext.user?.id,
        message: msg,
      },
      (response: any) => {}
    );
  };

  return { messagesArr, typingStatus, selectedUser, sendMessage, joinRoom };
};
