import React, { useState, useEffect, useContext } from "react";
import appAxios from "../../axios/axios";
import { Navbar } from "react-bootstrap";
import { useChat } from "../../hooks/useChat";
import AuthContext from "../../context/authContext";

interface IUser {
  id: number;
  name: string;
  userName?: string;
  role?: string;
  createdAt?: string;
  updatedAt?: string;
}

const getAllUsers = async () => {
  const response = await appAxios.get("/users/getAllusers");
  return response.data;
};

const Chat: React.FC = () => {
  const [users, setUsers] = useState<IUser[]>([]);
  const [message, setMessage] = useState<string>("");
  const authContext = useContext(AuthContext);
  const { messagesArr, typingStatus, selectedUser, sendMessage, joinRoom } =
    useChat(message);

  useEffect(() => {
    getAllUsers().then((res) => {
      setUsers(res.result);
    });
  }, []);

  const printInbox = () => {
    return users.map((ele, i) => (
      <div
        className={`inbox ${selectedUser?.name === ele.name ? "active" : ""}`}
        onClick={() => {
          joinRoom(ele);
        }}
        key={i}
      >
        <span>
          {ele.id === authContext.user.id ? `${ele.name}(self)` : ele.name}
        </span>
      </div>
    ));
  };

  return (
    <div className="row w-100 m-0">
      <div className="col-lg-3 col-md-4 p-0 inbox-container">
        {printInbox()}
      </div>
      <div className="col-lg-9 col-md-8 p-0 chat-container">
        {selectedUser && (
          <React.Fragment>
            <Navbar bg="secondary" className="text-white px-1 py-3">
              {selectedUser.name}
              {typingStatus}
            </Navbar>
            <div className="chat-bubble-container">
              {messagesArr.map((ele, i) => (
                <div
                  className={`chat-bubble-row ${
                    ele.userId === authContext.user?.id
                      ? "justify-content-end"
                      : "justify-content-start"
                  }`}
                  key={i}
                >
                  <div className="chat-bubble">{ele.message}</div>
                </div>
              ))}
            </div>
            <form
              onSubmit={(e) => {
                e.preventDefault();
                sendMessage(message);
              }}
              className="chat-input-container"
            >
              <input
                id="chat"
                placeholder="Send a message.."
                type="text"
                className="form-control border-0 mx-2"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
              />
              <button className="btn btn-primary btn-sm" type="submit">
                Send
              </button>
            </form>
          </React.Fragment>
        )}
      </div>
    </div>
  );
};

export default Chat;
